import React, { useContext, useState, useEffect } from "react";
import { PoolContext } from "../../Context/PoolContext";
import DomainCard from "./DomainCard";
import { useNavigate } from "react-router-dom";
import Loading from "../../images/loading.png";
const ShowDomains = (props) => {
  const navigate = useNavigate();
  const pc = useContext(PoolContext);
  const [isLoading, setIsLoading] = useState(true); // Initialize loading state

  useEffect(() => {
    // Simulating data fetching or processing
    setTimeout(() => {
      setIsLoading(false); // Set loading to false after data is ready
    }, 1000); // Adjust the timeout duration as needed
  }, []);

  const Goback = () => {
    navigate("/");
  };

  return (
    <div className="w-[95%] m-auto min-h-[90vh] rounded-md bg-white ">
      <div className="flex justify-start ml=0">
        <div
          onClick={Goback}
          className="ml-12 mt-10 bg-indigo-500 text-white px-4 py-2 rounded-md hover:bg-indigo-600 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-opacity-10"
        >
        <i className="fas fa-arrow-left"></i>
        </div>
      </div>
      <div className="show-pools flex justify-start items-start gap-2 h-4/5  overflow-y-auto overflow-x-hidden">
        <div className="m-10 p-3 pb-0 w-full h-full">
          <div className="flex justify-between mb-3 items-center">
            <h2 className="font-bold leading-7 text-gray-600 text-lg">
              Identity Providers
            </h2>
            <button
              onClick={() => navigate("/domain/domain-create-form")}
              className="bg-indigo-500 hover:bg-indigo-600 hover:text-gray-300 text-white rounded-md px-3 py-2 text-sm font-medium flex flex-col gap-2"
            >
              + Add new provider
            </button>
          </div>
          {isLoading ? (
              <div className="flex justify-center items-center h-full">
              <img
                src={Loading}
                alt="Loading..."
                className="w-12 h-12 animate-spin"
              />
            </div>
          ) : (
            <div className="m-5 flex gap-10">
              {pc.availableDomains.length > 0 ? (
                pc.availableDomains
                  .sort((a, b) => a.name.localeCompare(b.name))
                  .map((item) => (
                    <DomainCard
                      key={item.id}
                      id={item.id}
                      name={item.name}
                      providerId={item.providerId}
                      enabled={
                        item.config.enabled === "true" ? "Enabled" : "Disabled"
                      }
                    />
                  ))
              ) : (
                <div className="m-5 flex gap-10 w-full justify-center items-center">
                  No domains available
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ShowDomains;
